import { browserName } from '../../utils/utils';
import {chrome} from '../../utils/polyfill.ts';
import {GROUP_A, MSG_FIREFOX, MSG_IS_ONBOARDING_COMPLETE} from './app-consts';

function firefoxDebuggingWorkaround(msg, obj) {
    chrome.runtime.sendMessage({type: MSG_FIREFOX, payload: msg, obj: obj}, function(response) {
        //we dont actually care about the response
        //we just have to log messages via background because of
        //ridiculous firefox dev tools hanging so that you can't
        //inspect the popup page

        //this logged response can only be seen in chrome,
        //just to sanity check that this message went through
        console.debug("SF Msg sent:", response);
    });
}

function onboard() {
    chrome.runtime.sendMessage({type: MSG_IS_ONBOARDING_COMPLETE}, function(response) {
        if (!response || response.error) {
            firefoxDebuggingWorkaround("Onboarding failed: " + response);
            goToOnboarding();
        } else {
            firefoxDebuggingWorkaround("Is onboarding complete: ", response.success);
            if (response.success) {
                // Passing this an object to Group A allows us to not turn off protection
                // because we only assess the object values here and it is assumed true for the rest of the onboarding experience.
                // Only group B has the intention to turn off protection till they submit their email
                if (response.success.group) {
                    if (response.success.group === GROUP_A && response.success.result) {
                        goToDashboard();
                    } else {
                        goToOnboarding();
                    }
                } else {
                    goToDashboard();
                }
            } else {
                goToOnboarding();
            }
        }
    });
}

function goToDashboard() {
    firefoxDebuggingWorkaround("Pop redirecting to Dashboard");
    window.location.href = "app.html";
}

function goToOnboarding() {
    firefoxDebuggingWorkaround("Pop redirecting to Onboarding");
    if (browserName() == "Firefox") {
        window.location.href = "app.html";
    } else {
        window.location.href = "eventpages/email.html";
    }
}

document.addEventListener("DOMContentLoaded", function() {
    firefoxDebuggingWorkaround("Pop doc ready. Onboarding.");
    onboard();
});
